import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LayoutComponent } from './layout.component';
import { user_reflections } from './user_reflections.component';

const routes: Routes = [
    {
        path: '', component: user_reflections,
        children: [
            { path: '', component: user_reflections },
            { path: 'users/reflection', component: user_reflections },
            { path: 'edit/:id', component: user_reflections }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ReflectionRoutingModule { }